<template>
    <div class="m-fb-story" v-loading="loading">
        <el-tabs v-model="map_id" type="card" v-if="data && data.length">
            <el-tab-pane v-for="item in data" :label="item.Layer3Name" :key="item.MapID" :name="String(item.MapID)">
                <div class="m-story-basic">
                    <el-divider content-position="left">基本信息</el-divider>
                    <el-descriptions :column="3" border>
                        <el-descriptions-item label="名称">{{ item.OtherName }}</el-descriptions-item>
                        <el-descriptions-item label="地图ID">{{ item.MapID }}</el-descriptions-item>
                        <el-descriptions-item label="资料片">Level.{{ item.DivideLevel }}／{{ item.DivideName }}／{{ item.VersionName }}</el-descriptions-item>
                        <el-descriptions-item label="最低等级要求">{{ item.MinLevel }}</el-descriptions-item>
                        <el-descriptions-item label="入口" :span="2">{{ item.EnterWay }}</el-descriptions-item>
                        <el-descriptions-item label="简介">{{ item.Introduction | formartIntroduction }}</el-descriptions-item>
                    </el-descriptions>
                </div>
                <div class="m-story-boss">
                    <el-divider content-position="left">首领传说</el-divider>
                    <div class="u-list" v-if="boss_list && boss_list.length">
                        <div class="u-boss" v-for="(boss, i) in boss_list" :key="i">
                            <h5 class="u-name">
                                <svg xmlns="http://www.w3.org/2000/svg" width="833.7" height="833.7" viewBox="0 0 833.7 833.7" v-bind:class="'u-boss-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M416.8 0C208.5.1 39.6 169 39.6 377.399V555.2c0 9.399 4.4 18.199 11.9 23.899L213.7 702.2v101.5c0 16.6 13.4 30 30 30h24.1c16.6 0 30-13.4 30-30v-41.2c0-10.5 8.5-19 19-19s19 8.5 19 19v41.2c0 16.6 13.4 30 30 30h1c16.6 0 30-13.4 30-30v-41.2c0-10.5 8.5-19 19-19h2c10.5 0 19 8.5 19 19v41.2c0 16.6 13.4 30 30 30h1c16.6 0 30-13.4 30-30v-41.2c0-10.5 8.5-19 19-19s19 8.5 19 19v41.2c0 16.6 13.4 30 30 30H590c16.6 0 30-13.4 30-30V702.1L782.2 579c7.5-5.7 11.899-14.5 11.899-23.9V377.3C794 169 625.1.1 416.8 0zM259.1 558.1c-57.4 0-104-46.6-104-104 0-57.399 46.6-104 104-104s104 46.601 104 104c0 57.5-46.6 104-104 104zm187.3 76.5h-59.1c-13.7 0-22.4-14.7-15.8-26.7l29.6-53.699c6.8-12.4 24.7-12.4 31.5 0l29.601 53.699c6.5 11.999-2.201 26.7-15.801 26.7zm128.1-76.5c-57.4 0-104-46.6-104-104 0-57.399 46.6-104 104-104 57.399 0 104 46.601 104 104 0 57.5-46.5 104-104 104z"/></svg>
                                {{ boss.BOSS }}
                            </h5>
                            <p class="u-boss-detail" v-html="boss.Introduce"></p>
                            <el-button class="u-boss-more" type="primary" plain @click="loadSkills(boss)" size="mini" icon="el-icon-key">{{
                                !skill_list[boss.NPCID] ? "查看技能" : skill_status[boss.NPCID] ? "收起" : "展开"
                            }}</el-button>
                            <div class="u-boss-skills" v-show="skill_status[boss.NPCID]">
                                <template v-if="skill_list[boss.NPCID]">
                                    <div class="u-skill" v-for="skill in skill_list[boss.NPCID]" :key="skill.idkey">
                                        <h6 class="u-skill-name">※ {{ skill.SkillName }}</h6>
                                        <div class="u-skill-desc">{{ skill.Desc }}</div>
                                    </div>
                                </template>
                                <div class="u-skill-null" v-else><i class="el-icon-warning-outline"></i> 没有相关信息</div>
                            </div>
                        </div>
                    </div>
                </div>
            </el-tab-pane>
        </el-tabs>
        <el-alert title="未找到相关信息" type="info" show-icon v-else></el-alert>
    </div>
</template>

<script>
import { __ossMirror } from "@jx3box/jx3box-common/data/jx3box";
import { getInfo, getBoss, getBossSkills } from "@/service/getStory.js";
import { extractTextContent } from "@jx3box/jx3box-common/js/utils";
import compact from "lodash/compact";
export default {
    name: "Story",
    props: [],
    data: function() {
        return {
            loading: false,
            map_id: "",
            data: "",
            boss_list: "",
            skill_list: {
                // NPCID : []
            },
            skill_status: {},
        };
    },
    computed: {
        fb: function() {
            return this.$route.query.fb_name || this.$store.state.default_fb;
        },
        client: function() {
            return this.$store.state.client;
        },
        map_cover: function() {
            return __ossMirror + "pic/map/map_" + this.mapid + "_0.png";
        },
    },
    methods: {
        loadData: function() {
            this.loading = true;
            getInfo(this.fb, this.client)
                .then((res) => {
                    this.data = res?.data || [];
                    this.map_id = String(this.data?.[0]?.["MapID"]);
                    return this.map_id;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        loadSkills: function(boss) {
            let npc_id = boss?.NPCID;
            if (this.skill_list[npc_id]) {
                this.skill_status[npc_id] = !this.skill_status[npc_id];
                this.$forceUpdate();
            } else {
                // 合并技能列表
                let skills = [boss.Step1Skill, boss.Step2Skill, boss.Step3Skill, boss.Step4Skill, boss.szStep5Skill];
                skills = _.compact(skills);
                skills = skills.join(";");

                // 请求技能数据
                this.loading = true;
                getBossSkills(skills, this.client)
                    .then((res) => {
                        this.skill_list[npc_id] = res?.data || [];
                        this.skill_status[npc_id] = true;
                        this.$forceUpdate();
                    })
                    .finally(() => {
                        this.loading = false;
                    });
            }
        },
    },
    watch: {
        fb: function() {
            this.loadData();
        },
        map_id: function(id) {
            if (id) {
                this.loading = true;
                getBoss(id, this.client)
                    .then((res) => {
                        this.boss_list = res?.data || [];
                    })
                    .finally(() => {
                        this.loading = false;
                    });
            }
        },
    },
    filters: {
        formartIntroduction: function(str) {
            return extractTextContent(str)?.[0]?.text;
        },
    },
    mounted: function() {
        this.loadData();
    },
};
</script>

<style lang="less">
@import "../assets/css/story.less";
</style>
